<template>
  <view class="popup-container">
    <view :class="['mask','z-index-' + zIndex]" :catchtouchmove="true" v-if="showPopup"
          :data-component-info="componentInfo" @click="wxspopup.close"/>
    <view :catchtouchmove="true" :class="['popup-box popup-transition','z-index-' + zIndex]"
          :style="{top: popupTop + 'px',height: componentInfo.popupH + 'px'}">
      <view class="move-btn flex row"
            :data-component-info="componentInfo"
            id="popup-move-btn"
            @touchstart="wxspopup.touchstart"
            @touchmove="wxspopup.touchmove"
            @touchend="wxspopup.touchend"
            :style="{pointerEvents:showPopup?'auto':'none'}"
      >
        <view class="line"></view>
      </view>
      <slot/>
    </view>
  </view>
</template>
<script lang="wxs" module="wxspopup" src="./index.wxs"></script>

<script >
import { ref } from "vue"
import { mapGetters, mapState } from "vuex"

export default {
  emits:{
    "close":null
  },
  props: {
    zIndex: {
      type: Number,
      default: 1
    },
    height: {
      type: Number,
      default: 1000
    }
  },
  computed: {
    ...mapGetters('user', ['userInfo', 'token', 'userId']),
    ...mapState('mall', ['shoppingCart']),
    systemInfo() {
      return this.$store.getters.systemInfo;
    },
    componentInfo() {
      return {
        moveBtn: uni.upx2px(50),
        popupH: uni.upx2px(this.height),
        screenH: this.systemInfo.screenHeight
      }
    },
    popupTop() {
      return this.showPopup ? this.componentInfo.screenH - this.componentInfo.popupH : this.componentInfo.screenH
    }
  },
  setup(props,{emit}){
    const showPopup = ref(false)
    function handlePopup(res) {
      showPopup.value = res.show;
      emit("close", res.show)
    }
    function open() {
      showPopup.value = true;
    }
    function close() {
      showPopup.value = false;
    }
    return {
      showPopup,
      handlePopup,
      open,
      close
    }
  },
}
</script>
<style scoped lang="scss">
@import "./index.scss";
</style>
